:root {
  font-size: 62.5%;
}

*, *::before, *::after {
  box-sizing: border-box;
}

* {
  padding: 0;
  margin: 0;
  border: 0;
}

img {
  max-width: 100%;
}

body {
  display: grid;
  align-items: center;
  justify-content: center;
}

.attribution {
  text-align: center;
  max-height: 2em;
}

.card-container {
  margin: 5em auto;
}

.card {
  display: grid;
  padding: 2.4em;
  margin: auto;
  justify-items: left;
  max-width: 33rem;
}
.card h1 {
  margin: 0.5em 0;
}
.card p {
  margin: 0.5em 0;
  padding-right: 1.5em;
}

@media (min-width: 50rem) {
  body {
    height: 100vh;
  }

  .card-container {
    display: flex;
    margin: auto;
  }

  p {
    padding-bottom: 3em;
  }
}
:root {
  --orange: hsl(31, 77%, 52%);
  --cyan: hsl(184, 100%, 22%);
  --dark-cyan: hsl(179, 100%, 13%);
  --white-p: hsla(0, 0%, 100%, 0.75);
  --gray: hsl(0, 0%, 95%);
  --font-family-700: "Big Shoulders Display", cursive;
  --font-family-400: "Lexend Deca", sans-serif;
}

body {
  background-color: var(--gray);
  font-family: var(--font-family-400);
  font-size: 1.5rem;
}

#card1 {
  color: var(--orange);
  background: var(--orange);
}

#card2 {
  color: var(--cyan);
  background: var(--cyan);
}

#card3 {
  color: var(--dark-cyan);
  background: var(--dark-cyan);
}

.card h1, .card p {
  color: var(--gray);
}
.card h1 {
  font-family: var(--font-family-700);
  font-size: 4.2rem;
}
.card p {
  opacity: 70%;
}

.attribution a {
  text-decoration: none;
}

.learn-button {
  background: var(--gray);
  padding: 1em 2em;
  margin: 1em 0;
  border-radius: 1.5em;
  border: solid var(--gray) 0.2em;
  text-align: center;
  font-size: 1.5rem;
  font-family: var(--font-family-400);
  text-decoration: none;
  color: inherit;
  transition: ease-in-out 0.4s;
}

.learn-button:hover {
  background: transparent;
  color: var(--gray);
}